
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  text-decoration: none; }

html {
  color: #141414;
  font-family: sans-serif, Arial, serif; }

body {
  margin: auto;
  font-size: 16px;
  height: 100%;
  background-color: #f0f0e6; }

.App {
  text-align: center;
  max-width: 100%; }

@font-face {
  font-family: merienda;
  src: url(/react-bitCoin/static/media/Merienda-Regular.56cb3669.ttf); }

@font-face {
  font-family: quando;
  src: url(/react-bitCoin/static/media/Quando-Regular.849e6795.ttf); }

.loader-all {
  margin: 0 auto; }
  .loader-all img {
    height: 100px;
    width: 100px; }

/********** Nav **********/
.Nav_menu {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 100%;
  height: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0; }

.Nav_menu ul {
  margin: 0 auto;
  padding: 0; }

.Nav_link:link {
  color: black;
  font-size: 1.2em;
  text-decoration: none;
  padding-right: 10px; }

.Nav_link:hover {
  color: #ff8800; }

.Nav_link:active {
  color: teal; }

.activeRoute {
  color: red; }

/**************** Pages ****************/
/********** Contact Page **********/
/********** Filter **********/
.filter-container {
  margin: 6px 0px 6px 0px; }
  .filter-container input {
    font-size: 1.1rem;
    padding: 2px 4px;
    border: 0.5px solid #444444;
    background-color: #f0f0e6; }
    .filter-container input:focus {
      background-color: #ebf4fa;
      outline: 1px solid rgba(0, 0, 0, 0); }

/********** List **********/
.new-contact {
  position: fixed;
  bottom: 10px;
  right: 10px;
  height: 50px;
  width: 50px;
  margin: 0 auto 14px auto;
  background-color: #6c90d1;
  border-radius: 50%;
  transition: opacity 0.3s; }
  .new-contact:hover {
    opacity: 0.9; }
  .new-contact div {
    width: 100%;
    height: 100%; }
    .new-contact div img {
      position: relative;
      top: 6px;
      left: 2px;
      width: 70%;
      height: auto; }

@media (min-width: 500px) {
  .new-contact {
    position: relative;
    bottom: 0;
    right: 0; } }

.contact-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  grid-gap: 10px;
  margin: 0 10px;
  list-style-type: none;
  padding: 0; }
  section .contact-list {
    margin-bottom: 20px; }
  .contact-list li {
    padding: 2px 2px 6px 2px;
    font-family: quando, Arial, Helvetica, sans-serif;
    border-radius: 2px;
    color: #f0f0e6;
    background-color: #395b99; }

@media (min-width: 500px) {
  .contact-list {
    grid-gap: 20px;
    margin: 0 20px; } }

/********** Details **********/
.user-details {
  margin: 10px 20px 0 20px; }
  .user-details .user-img {
    -webkit-flex-grow: 1;
            flex-grow: 1; }
  .user-details .details {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    margin: auto;
    text-align: left; }

.options {
  width: 200px;
  margin: 10px auto; }
  .options img {
    cursor: pointer; }

/********** Edit **********/
.options-edit img {
  cursor: pointer;
  margin: 4px 20px; }

.contact-container {
  max-width: 80%;
  margin: 0 auto; }

.contact-container > p {
  margin: 6px 0; }

.contact-form > input:not(:last-child) {
  margin-bottom: 6px;
  font-size: 1.1em;
  padding: 4px;
  border: 1px solid #616161; }

.contact-form > input:last-child {
  cursor: pointer;
  margin: 14px 0 4px 0;
  font-size: 1.1em;
  padding: 4px 0;
  border: none;
  background-color: #ffa149;
  transition: background-color 0.3s; }

.contact-form > input:last-child:hover {
  background-color: #f89940; }

.contact-form > input:not(:last-child):focus {
  background-color: #fff3e7;
  outline: 1px solid rgba(0, 0, 0, 0); }

